// src/component/blogService/PostCard.tsx
'use client';

import React from "react";
import styles from "@/styles/blogService/post.module.css";
import { PostEntity } from "@/types/blogService/blogType";
import { useRouter } from "next/navigation";

interface PostCardProps {
  post: PostEntity;
}

export default function PostCard({ post }: PostCardProps) {
  const router = useRouter();

  // âœ… HTML íƒœê·¸ ì œê±° + HTML ì—”í‹°í‹° ë””ì½”ë”©
  const stripHtml = (html: string): string => {
    if (!html) return "";

    // ë¸Œë¼ìš°ì € í™˜ê²½ì—ì„œë§Œ ì‹¤í–‰
    if (typeof window !== 'undefined') {
      // ì„ì‹œ div ìš”ì†Œë¥¼ ì‚¬ìš©í•˜ì—¬ HTML íŒŒì‹± ë° ì—”í‹°í‹° ë””ì½”ë”©
      const temp = document.createElement('div');
      temp.innerHTML = html;

      // í…ìŠ¤íŠ¸ë§Œ ì¶”ì¶œ (HTML íƒœê·¸ ì œê±° + ì—”í‹°í‹° ìë™ ë””ì½”ë”©)
      const text = temp.textContent || temp.innerText || '';

      // ì—°ì†ëœ ê³µë°±ì„ í•˜ë‚˜ë¡œ ì¶•ì•½
      return text.replace(/\s+/g, ' ').trim();
    }

    // ì„œë²„ ì‚¬ì´ë“œ ë Œë”ë§ ëŒ€ì‘ (fallback)
    return html
    .replace(/<[^>]*>/g, ' ')
    .replace(/&nbsp;/g, ' ')
    .replace(/&lt;/g, '<')
    .replace(/&gt;/g, '>')
    .replace(/&amp;/g, '&')
    .replace(/&quot;/g, '"')
    .replace(/&#39;/g, "'")
    .replace(/\s+/g, ' ')
    .trim();
  };

  const plainText = stripHtml(post.content);
  const previewText = plainText.length > 120 ? plainText.substring(0, 120) + "..." : plainText;

  const dateStr = new Date(post.createdAt).toLocaleDateString('ko-KR', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  });

  return (
      <article
          className={styles.card}
          onClick={() => router.push(`/post/${post.postId}`)}
          style={{ cursor: "pointer" }}
      >
        <div className={styles.thumbnailWrapper}>
          {post.thumbnail ? (
              // ì¸ë„¤ì¼ì´ ìˆì„ ê²½ìš°: ê¸°ì¡´ ë¡œì§
              <>
                <img
                    src={post.thumbnail}
                    alt={post.title}
                    className={styles.thumbnail}
                    loading="lazy"
                    onError={(e) => (e.currentTarget.style.display = "none")}
                />
                <div className={styles.overlay}>
                </div>
              </>
          ) : (
              // ì¸ë„¤ì¼ì´ ì—†ì„ ê²½ìš°: Placeholder ë Œë”ë§
              <div className={styles.noThumbnailPlaceholder}>
              </div>
          )}
        </div>


        <div className={styles.content}>
          <h3 className={styles.title}>{post.title}</h3>
          <p className={styles.preview}>{previewText}</p>
          <div className={styles.meta}>
            <span className={styles.author}>{post.authorId}</span>
            <span className={styles.date}>{dateStr}</span>
            <span className={styles.views}>ì¡°íšŒ {post.viewCount.toLocaleString()}</span>
          </div>

          {(post.tags ?? []).length > 0 && (
              <div className={styles.tags}>
                {(post.tags ?? []).map((tag, i) => (
                    <span
                        key={i}
                        className={styles.tag}
                        onClick={(e) => {
                          e.stopPropagation();
                          router.push(`/tag/${encodeURIComponent(tag)}`);
                        }}
                    >
                        #{tag}
                    </span>
                ))}
              </div>
          )}
        </div>
      </article>
  );
}